<script>
import { ref, onMounted, onUnmounted, watch, computed } from 'vue'
import Swiper from '../../assets/swiper/swiper-bundle.min.js'
import NoData from "../../components/global/NoData.vue"
import { useRouter } from "vue-router"
import { monthHandle, monthHandle2, errorHandle } from '@/utils/handle'
import { apiGetWorkOrderList } from "@/api/api"
import { isBetweenBottom, isBetweenBottom2 } from "@/utils/helper"

export default {
  setup() {
    const router = useRouter()
    const page1 = ref(1)
    let totalData1 = 0
    const page2 = ref(1)
    let totalData2 = 0
    const page3 = ref(1)
    let totalData3 = 0
    let getApiTimer = null
    let index = ref()

    // 頁籤
    function initSwiper(slidesPerView) {
      var tabSwiper = new Swiper('.ui-tab', {
        slidesPerView: slidesPerView,
        freeMode: true,
        watchSlidesVisibility: true,
        watchSlidesProgress: true,
      });

      var pageSwiper = new Swiper('.ui-page', {
        thumbs: {
          swiper: tabSwiper,
        },
      });
    }

    let data1 = ref([])
    let data2 = ref([])
    let data3 = ref([])

    const dateToString = (val) => {
      return [val.getFullYear(), val.getMonth() + 1, val.getDate()]
    }

    //處理data的日期
    const dateHandle = (data) => {
      data.forEach(item => {
        let res = monthHandle(item.dates)
        item.newDates = res.newDates
        item.info = res.info
        item.time = res.time
        item.monthInfo = Object.keys(item.info)
      })
    }
    const dateHandle2 = (data) => {
      data.forEach(item => {
        let res = monthHandle2(item.dates)
        item.newDates = res.newDates
        item.info = res.info
        item.time = res.time
        item.monthInfo = Object.keys(item.info)
      })
    }

    onMounted(async () => {
      // 頁籤數
      initSwiper(3)
      data1.value = await apiGetWorkOrderList({ status: 1, page: page1.value, limit: 20 })
      totalData1 = data1.value.data.total
      data1.value = data1.value.data.list
      data2.value = await apiGetWorkOrderList({ status: 2, page: page2.value, limit: 20 })
      totalData2 = data2.value.data.total
      data2.value = data2.value.data.list
      data3.value = await apiGetWorkOrderList({ status: 3, page: page3.value, limit: 20 })
      totalData3 = data3.value.data.total
      data3.value = data3.value.data.list

      dateHandle(data1.value)
      dateHandle(data2.value)
      dateHandle2(data3.value)

      //每0.5S監測是否在底部
      getApiTimer = setInterval(() => {
        const child = document.querySelectorAll('.swiper-slide-thumb-active')[0]
        index.value = getElementIndex(child)
        if (isBetweenBottom()) {
          if (data1.value.length < totalData1) { handleScroll1() }
          if (data2.value.length < totalData2) { handleScroll2() }
          if (data3.value.length < totalData3) { handleScroll3() }
        }
      }, 500)
    })

    function getElementIndex(element) {
      return Array.from(element.parentNode.children).indexOf(element)
    }


    //滾動載入資料
    const handleScroll1 = async () => {
      page1.value += 1;
      let newData = ref()
      newData.value = await apiGetWorkOrderList({ status: 1, page: page1.value, limit: 20 })
      data1.value = data1.value.concat(newData.value.data.list)
      dateHandle(data1.value)
    };
    const handleScroll2 = async () => {
      page2.value += 1;
      let newData = ref()
      newData.value = await apiGetWorkOrderList({ status: 2, page: page2.value, limit: 20 })
      data2.value = data2.value.concat(newData.value.data.list)
      dateHandle(data2.value)
    };
    const handleScroll3 = async () => {
      page3.value += 1;
      let newData = ref()
      newData.value = await apiGetWorkOrderList({ status: 3, page: page3.value, limit: 20 })
      data3.value = data3.value.concat(newData.value.data.list)
      dateHandle2(data3.value)
    };
    //取消監控
    onUnmounted(() => clearInterval(getApiTimer))

    const goto = (val) => {
      router.push({ path: val })
    }

    watch(() => index.value, async () => {
      document.documentElement.scrollTop = 0
    })

    return { goto, data1, data2, data3 };
  },
  components: { NoData }
};


</script>

<template>
  <div class="orderList">
    <div class="main">
      <div class="swiper-container ui-tab">
        <div class="swiper-wrapper">
          <div class="swiper-slide"><span>新工單</span></div>
          <div class="swiper-slide"><span>已回覆</span></div>
          <div class="swiper-slide"><span>執行中</span></div>
        </div>
      </div>
      <div class="swiper-container ui-page">
        <div class="swiper-wrapper">
          <div class="swiper-slide">
            <NoData v-if="data1.length == 0" />
            <div class="order-container" v-if="data1.length > 0">
              <div class="card" v-for="item in data1" :key="item.id">
                <div class="card-body">
                  <a @click="goto(`/order/info/${item.id}`)" class="card-link">
                    <div class="card-title">{{ item.serviceRequirementName }}</div>
                    <div class="card-date">
                      <div class="d-flex">
                        <div class="col-left">照顧日期：</div>
                        <div class="col-right">{{ item?.monthInfo?.[0] }}/{{
                            item?.info?.[item?.monthInfo?.[0]].join('、')
                        }}
                        </div>
                      </div>
                      <div class="d-flex" v-if="item?.monthInfo?.length == 2">
                        <div class="col-left"></div>
                        <div class="col-right">{{ item?.monthInfo?.[1] }}/{{
                            item?.info?.[item?.monthInfo?.[1]].join('、')
                        }}
                        </div>
                      </div>
                    </div>
                    <div class="card-time">照顧時段：{{ (item.time) }}</div>
                  </a>
                </div>
              </div>
            </div>
          </div>
          <div class="swiper-slide">
            <NoData v-if="data2.length == 0" />
            <div class="order-container" v-if="data2.length > 0">
              <div class="card" v-for="item in data2" :key="item.id">
                <div class="card-body">
                  <a @click="goto(`/order/info/${item.id}`)" class="card-link">
                    <div class="card-title">{{ item.serviceRequirementName }}</div>
                    <div class="card-date">
                      <div class="d-flex">
                        <div class="col-left">照顧日期：</div>
                        <div class="col-right">{{ item?.monthInfo?.[0] }}/{{
                            item?.info?.[item?.monthInfo?.[0]].join('、')
                        }}
                        </div>
                      </div>
                      <div class="d-flex" v-if="item?.monthInfo?.length == 2">
                        <div class="col-left"></div>
                        <div class="col-right">{{ item?.monthInfo?.[1] }}/{{
                            item?.info?.[item?.monthInfo?.[1]].join('、')
                        }}
                        </div>
                      </div>
                    </div>
                    <div class="d-flex justify-content-between">
                      <div class="card-time">照顧時段：{{ item.time }}</div>
                      <div class="card-rate">回覆率：<span class="percent">{{ item.responseRate }}%</span></div>
                    </div>
                  </a>
                </div>
              </div>
            </div>
          </div>
          <div class="swiper-slide">
            <NoData v-if="data3.length == 0" />
            <div class="order-container" v-if="data3.length > 0">
              <div class="card" v-for="item in data3" :key="item.id">
                <div class="card-body">
                  <a @click="goto(`/order/info3/${item.id}`)" class="card-link">
                    <div class="card-title">{{ item.careTaker }}({{ item.detail.gender == "Male" ? "男性" : "女性" }}/{{
                        item.detail.age
                    }}歲/{{ item.detail.height }}cm/{{ item.detail.weight }}kg)</div>
                    <div class="card-kind">服務類型：{{ item.serviceRequirementName }}</div>
                    <div class="card-date">
                      <div class="d-flex">
                        <div class="col-left">照顧日期：</div>
                        <div class="col-right">{{ item.monthInfo[0] }}/<span
                            v-for="(item2, index2) in item.info[item.monthInfo[0]]" :key="index2"><span
                              :class="item2[1] == 1 ? 'finish' : ''">{{ item2[0] }}</span><span
                              v-if="index2 < (item.info[item.monthInfo[0]].length - 1)">、</span></span>
                        </div>
                      </div>
                      <div class="d-flex" v-if="item.monthInfo.length == 2">
                        <div class="col-left"></div>
                        <div class="col-right">{{ item.monthInfo[1] }}/<span
                            v-for="(item2, index2) in item.info[item.monthInfo[1]]" :key="index2"><span
                              :class="item2[1] == 1 ? 'finish' : ''">{{ item2[0] }}</span><span
                              v-if="index2 < (item.info[item.monthInfo[1]].length - 1)">、</span></span>
                        </div>
                      </div>
                    </div>
                    <div class="d-flex justify-content-between">
                      <div class="card-time">照顧時段：{{ item.time }}</div>
                      <div class="card-rate">完成率：<span class="percent">{{ item.completionRate }}%</span></div>
                    </div>
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";
@import "@/assets/custom/mixins";
@import "@/assets/swiper/swiper-bundle.min.css";

.orderList {
  margin-top: $header-height;
  margin-bottom: calc($footer-height + 15px);
}
.ui-page{
  height: calc(100vh - $header-height - $footer-height - 15px);
}
</style>